/*----------------------------------------------------------------*/
/* Import Theme
/*----------------------------------------------------------------*/
@import '../../../../bower_components/theme/core/bootstrap3/less/variables.less';
@import '../../../../bower_components/theme/core/bootstrap3/less/mixins/vendor-prefixes.less';
@import '../../../../bower_components/theme/universal/styles/mixins.less';

/*----------------------------------------------------------------*/
/* labeled input custom styles
/*----------------------------------------------------------------*/
.lp-labeled-input {
  border-radius: 3px;

  .text-red {
    color: @brand-danger;
  }

  .text-green {
    color: @brand-success !important;
  }
}

/*----------------------------------------------------------------*/
/* otp-check styles
/*----------------------------------------------------------------*/
.lp-otp-check {
  
  .otp-content {
    padding: 1em;
  }

  .text-red {
    color: @brand-danger;
  }

  .input-invalid {
    color: @brand-danger;
  }

  .verify-button {
    
  }
  
  .otp-nav {
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    padding: 1em;
    background-color: @gray-lighter;
  }

  .channel-selector {
    margin: 3em 0;
    
    .cell {
      margin-bottom: 1em;
    }
    
    .custom-radio {
      margin-bottom: 2em;
      .radio-label {
        display: inline-block;
        position: relative;
        padding-left: 0.7em;
        .translate(0, -20%);
        .radio-label-main {
          color: black;
        }
      }
      
      span {
        vertical-align: middle;
      }
    }
    
  }

  /* ----------- MOBILE ---------- */
  &.is-mobile {

  }

  /* ----------- DESKTOP ---------- */
  &.is-desktop {
    .close-button {
      margin: 5px;
      color: @gray-light;
      cursor: pointer;
    }
  }
}
